	module.exports = {"data":{"markdownRemark":{"html":"<blockquote>\n<p>Message 1552 : Sat Dec 15 (Blent)      :: Lunaron and Bazarat</p>\n</blockquote>\n<p>Long had Lunaron remained in his prison, blockaded in by the weak\ngoblin tribe that was unfortunate enough to play host. Long had he\nsought for escape, throwing himself against the barricade, the walls,\nand the ground until his bones cracked and his head rang. This\nevening, however, was different.</p>\n<p>The goblin leader was used to hearing the growls, crashing sounds, and\nhowling coming from behind the barricade. He was used to the periods of\nquiet when the great beast wore itself out and collapsed unconscious\nfor long periods of time. What he wasn't used to was the piercing,\ndrawn-out scream that echoed through his caves, waking his tribe and\nsending fear through his ranks.</p>\n<p>His menial servant dead, throat ripped out, Lunaron started throwing\nhimself against the rock slab covering the ground in his prison,\nunceasing in his rage. Suddenly it gave way, and Lunaron found himself\nthrown down into a small tunnel beneath the caves of the goblin tribe.\nThoughts of freedom suddenly wiped all thoughts of revenge against his\nnow-former host out of his mind, and Lunaron bounded through the\ntunnel, seeking an exit.</p>\n<p>Bazarat lay curled in his room in the tower, out of his mind as the\nlycanthropy ravaged his once-human body. Faint screams and cries for\nmercy rung endlessly in his ears, perhaps nothing more than the echoes\nfrom the carnage he wrought on the poor villagers. Suddenly, a louder,\ndeeper scream echoed through the room, followed by a terrific crashing\nsound. Bazarat sprung to his feet, looking wildly about.</p>\n<p>The gatekeeper dead, Lunaron threw his bulk against the gate until it\ncollapsed inwards. He made his way directly towards the tower in the\nsoutheastern corner of the village, drawn by something meaningful\nyet inexplicable.</p>\n<p>As Bazarat stood looking around his small chamber, the trapdoor in the\nfloor suddenly burst open and a mass of fur, claws, and unyielding\nmuscle sprang through. Before he could react, he was fighting for his\nlife against a beast just like himself, yet far larger.</p>\n<p>The fell beast lay sleeping in its prison, used to long periods of\ninactivity and quiet between feedings. It was a rude, yet not entirely\nunwelcome awakening, then, when the boulder was suddenly thrown aside\nand his master crashed into the room, bleeding heavily.</p>\n<p>Faced now with a fell beast in addition to the other werewolf, Lunaron\nfound himself on the losing end of the battle. He began to retreat\ntowards the gate leading out of the village, and it was here that he\ntook the worst beating, barely managing to flee into the surrounding\nforest, bleeding heavily.</p>\n<p>The intruder gone from his village, his pet back in its chamber,\nBazarat began to prowl the streets, weakened yet victorious.</p>\n<p>Finding an opening at the bottom of the sinkhole, Lunaron dragged\nhimself through, and continued only a short way before falling down a\nshaft, where he lay unconscious for quite some time.</p>\n<p>(OOC: Bazarat has changed slightly, and has slightly weakened. Lunaron\nhas weakened significantly, and moved. To maintain balance, Lunaron (and\nBazarat) have gained an interesting new ability that may make them hard\nto solo. Moreover, certain features in Lunaron's new area may make it\ndangerous for a super mob group should a PK group find them.</p>\n<p>Bazarat and his pet the fell beast will interact slightly differently\nnow.</p>\n<p>Bazarat's werewolf fur has improved very subtly. No trade-ins will be\nnecessary.</p>\n<p>A few rooms in the underground cave system near the new Uruk mage tower\nhave changed. You have been warned.</p>\n<p>A few shop inventories, for both sides of the war, have changed slightly;\nthis is related to Lunaron's move.</p>\n<p>P>news next\nMessage 1553 : Mon Jan 28 (Azriya)     :: Assorted fixes, changes, and updates</p>\n<p>A number of things have been tweaked recently.  Following is a list.</p>\n<ul>\n<li>Added \"rank\" command.  Shows your rank and 3 people above and\nbelow you.</li>\n<li>Fame war has been redone.</li>\n<li>Fame all now lists all kills within past 24 hours instead of\njust since last reboot.</li>\n<li>Swim skill has been redone.  The skill is slightly cheaper\nto learn, and those with 0% swim can no longer enter water rooms.</li>\n<li>Autodelete no longer affects L20 characters.  Still works as\nbefore on less than L20.</li>\n<li>Containers are no longer subject to decay upon death.</li>\n</ul>\n<p>More to come shortly.  Thanks to Vakaur and Ingolemo for this work.</p>\n<p>Comments, questions, complaints, mudmail me.\nAzriya</p>","frontmatter":{"title":"News 1552: Lunaron and Bazarat","date":"October 09, 2017","author":null,"desc":null}}},"pathContext":{"path":"/news/1552"}}

